<template>
	<div class="seller" ref="sellerWrapper">
		<div>
			<div class="basicInfo">
				<div class="basicInfotop">
		    		<p class="sellername">{{seller.name}}</p>
		            <p class="rating">
		            	<m-star class="star" :score = 'seller.score'></m-star>&nbsp;&nbsp;
		            	<span>({{seller.ratingCount}})&nbsp;&nbsp;月售{{seller.sellCount}}单</span>
		            </p>
		            <div class="collect" @click="show">
		             	<i class="icon iconfont icon-xin1" ref="Xin"></i>
		            	<p class="collect-text" ref="collection">收藏</p>
		            </div>
		    	</div>
		    	<div class="basicInfobottom">
		    		<div class="one">
		    			<p class="onea">起送价</p>
		    			<p class="oneb">
		    				<span class="price1">{{seller.minPrice}}</span>
		    				<span class="unit1">元</span>
		    			</p>
		    		</div>
		    		<div class="two">
		    			<p class="twoa">商家配送</p>
		    			<p class="twob">
		    				<span class="price2">{{seller.deliveryPrice}}</span>
		    				<span class="unit2">元</span>
		    			</p>
		    		</div>
		    		<div class="three">
		    			<p class="threea">平均配送时间</p>
		    			<p class="threeb">
		    				<span class="price3">{{seller.deliveryTime}}</span>
		    				<span class="minute">分钟</span>
		    			</p>
		    		</div>
		    	</div>
			</div>
			<div class="middleline"></div>
			<div class="noticeInfo">
				<p class="noticeName">公告和活动</p>
				<div class="noticeText">{{seller.bulletin}}</div>
				<ul class="active">
					<li class="activeLi" v-for="(v,index) in seller.supports">
						<img  :src="url[index]" class="liImg">
						<p class="activeText">{{v.description}}</p>
					</li>
				</ul>
			</div>
			<div class="middleline"></div>
			<div class="shijing">
				<p class="shijingname">商家实景</p>
				<div class="shijingimg" ref="shijingWrapper">
					<ul class="imgul">
						<li class="imgli" v-for="(v,index) in seller.pics">
							<img :src="v" class="imgI">
						</li>
					</ul>
				</div>
			</div>
			<div class="middleline"></div>
			<div class="huodonginfo">
				<p class="huodongname">商家信息</p>
				<ul class="huodongul">
					<li class="huodongli" v-for="(v,index) in seller.infos">
						<p class="huodongtext" >{{v}}</p>
					</li>
				</ul>
			</div>
		</div>
		<shopcart></shopcart>
	</div>
</template>
<script>
	import {mapGetters} from 'vuex'
	import mStar from '../star/Star_m.vue'
	import shopcart from '../Shopcart/Shopcart.vue'
	export default{
		data(){
			return {
				url:[
					'../../../static/imgs/decrease.png',
					'../../../static/imgs/discount.png',
					'../../../static/imgs/special.png',
					'../../../static/imgs/invoice.png',
					'../../../static/imgs/guarantee.png'
				]
			}
		},
		created(){
			this.$store.dispatch('getGoods');
			this.$nextTick( () => {
				setTimeout(() => {
					this._initSeller();
					this._initshijing();
				},100)
			})
		},
		components:{
			'm-star':mStar,
			shopcart
		},
		computed:{
			...mapGetters([
				'seller'
			])
		},
		methods:{
			_initSeller(){
				this.sellBS=new BScroll(this.$refs.sellerWrapper,{click:true});
			},
			_initshijing(){
				this.shijingBS=new BScroll(this.$refs.shijingWrapper,{
					click:true,
					scrollX:true
				});
			},
			show(){
				if(this.showXin){
					this.$refs.Xin.style.color="rgb(240,20,20)";
					this.$refs.collection.innerHTML="已收藏";
					this.showXin=false;
				}else{
					this.$refs.Xin.style.color="rgb(147,153,159)";
					this.$refs.collection.innerHTML="收藏";
					this.showXin=true;
				}
			}
		}
	}
</script>
<style lang="less" scoped>
	.seller{
		height: 8.8rem;
		overflow: hidden;
		margin-bottom:1.2rem;
		.basicInfo{
			width:100%;
         	height:3.0rem;
         	.basicInfotop{
         		height:1.5rem;
         		margin:0 0.36rem;
         		border-bottom:0.02rem solid rgba(0,0,0,0.2);
         		position:relative;
         		.sellername{
         			font-size:0.28rem;
         			padding-top: 0.36rem;
         			padding-bottom: 0.16rem;
         			color: rgb(7, 17, 27);
         		}
         		.rating{
                    font-size: 0.20rem;
					position: relative;
                    .star{
                    	display:inline-block;
                    }
                    span{
                    	font-size: 0.2rem;
                    	color:rgb(77, 85, 93);
                    	position: absolute;
                    	top:0.1rem;
                    }
         		}
         		.collect{
         			position:absolute;
         			top:0.4rem;
         			width: 0.8rem;
         			right: 0.36rem;
         			text-align:center;
                   	i{
                   		font-size: 0.48rem;
                   		color: rgb(147, 153, 159);
                   		line-height: 0.48rem;
                   	}
                   .collect-text{
                   	   font-size: 0.24rem;
                   	   color: rgb(147, 153, 159);
                   	   line-height: 0.2rem;
                   	   margin-top: 0.08rem;
                   }
         		}
         	}
         	.basicInfobottom{
         		display:flex;
         		.one{
         			width: 33%;
         			text-align: center;
         			.onea{
         				font-size: 0.2rem;
         				padding-top: 0.36rem;
         				padding-bottom: 0.08rem;
         				color:rgb(147, 153, 159);
         			}
         			.oneb{
         				font-size: 0.48rem;
         				.unit1{
         					font-size: 0.2rem;
         				}
         			}
         		}
         		.two{
         			width: 33%;
         			text-align: center;
         			.twoa{
         				font-size: 0.2rem;
         				padding-top: 0.36rem;
         				padding-bottom: 0.08rem;
         				color:rgb(147, 153, 159);
         			}
         			.twob{
         				font-size: 0.48rem;
         				.unit2{
         					font-size: 0.2rem;
         				}
         			}
         		}
         		.three{
         			width: 33%;
         			text-align: center;
         			.threea{
         				font-size: 0.2rem;
         				padding-top: 0.36rem;
         				padding-bottom: 0.08rem;
         				color:rgb(147, 153, 159);
         			}
         			.threeb{
         				font-size: 0.48rem;
         				.minute{
         					font-size: 0.2rem;
         				}
         			}
         		}
         	}
        }
        .middleline{
        	width: 100%;
        	height: 0.29rem;
        	background-color: rgba(7, 17, 27, 0.1);
        	border-top:1px solid rgba(27, 17, 27, 0.1);
        	border-bottom:1px solid rgba(27, 17, 27, 0.1);
        }
        .noticeInfo{
			.noticeName{
				font-size: 0.32rem;
				padding-top: 0.36rem;
				padding-bottom: 0.16rem;
				margin-left: 0.36rem;
			}
			.noticeText{
				margin:0 0.6rem;
				font-size: 0.24rem;
				line-height: 0.48rem;
				color:red;
				padding-bottom: 0.24rem;
			}
			.activeLi{
				height: 0.96rem;
				margin: 0 0.36rem;
				border-top:0.02rem solid rgba(0, 0, 0, 0.1);
				display: flex;
				.liImg{
					display: block;
					width: 0.4rem;
					height: 0.4rem;
					padding-top: 0.28rem ;
					padding-left: 0.24rem;
				}
				.activeText{
					margin-left:0.12rem;
					font-size: 0.24rem;
					line-height: 0.96rem;
				}
			}
        }
        .shijing{
        	.shijingname{
        		font-size: 0.32rem;
        		padding-top: 0.36rem; 
        		padding-bottom: 0.16rem;
        		margin-left: 00.36rem;
        	}
        	.shijingimg{
        		margin: 0 0.36rem;
        		padding-bottom: 0.36rem;
        		height: 1.08rem;
        		overflow: hidden;
        		.imgul{
        			display: flex;
        			width:10rem;
        			.imgI{
        				width: 2.4rem;
        				height: 1.3rem;
        				margin-right: 0.12rem;
        			}
        		}
        	}
        }
        .huodonginfo{
        	.huodongname{
        		font-size: 0.32rem;
        		padding-top: 0.36rem;
        		padding-bottom: 0.16rem;
        		margin-left: 0.36rem;
        	}
        	
        	.huodongli{
        		height: 0.96rem;
        		padding-top:0.32rem;
        		border-top:0.02rem solid rgba(0, 0, 0, 0.1);
        		.huodongtext{
	        		margin: 0 0.6rem;
	        		font-size: 0.24rem;
	        		line-height: 0.48rem;
	        		color: rgb(7, 17, 27); 
	        	}
        	}
        }
    }
	
</style>


