<template>
	<div class="ratings">
		<div class="rating-content">
		    <div class="ratingleft">
		    	<p class="numscore">{{seller.score}}</p>
		    	<p class="numscoretext">综合评分</p>
		    	<p class="rankRate">高于周边商家{{seller.rankRate}}%</p>
		    </div>
			<div class="ratingright">
                <div class="serviceScore">
                    <span class="text">服务态度</span>
                    <m-star  :score="seller.serviceScore"></m-star>
                    <span class="num">{{seller.serviceScore}}</span>
                </div>
                <div class="foodScore">
                    <span class="text">商品质量</span>
                    <m-star  :score="seller.foodScore"></m-star>
                    <span class="num">{{seller.foodScore}}</span>
                </div>
                <div class="deliveryTime">
                    <span class="text">送达时间</span>
                    <span class="time">{{seller.deliveryTime}}分钟</span>
                </div>
            </div>
		</div>
        <div class="line"></div>
        <div class="btn-content">
            <div class="btn">
                <div class="btn1" @click="getAll" :class="{'getAll':'getAll'===cc}">全部<span>{{ratings.length}}</span></div>
                <div class="btn2" @click="ok" :class="{'ok':'ok'===cc}">满意<span>{{countnum}}</span></div>
                <div class="btn3" @click="no" :class="{'no':'no'===cc}">不满意<span>{{ratings.length-countnum}}</span></div>
            </div>
            <div class="btn-text">
                <i class="iconfont icon-duihao icon" :class="{'checked':flag}" @click="changeFlag"></i>
                <span class="text">只看有内容的评价</span>
            </div>
        </div>
        <div class="midline"></div>
        <div class="ratingsinfo" ref = "ratingsWrapper">
            <ul>
                <li v-for="v in ratLists" class="menu-li" v-show="all || yes === v.rateType">
                    <div class='headDiv'>
                        <img :src="v.avatar" class="headImg">
                    </div>
                    <div class="rightdiv">
                        <p class="itemusername">{{v.username}}</p>
                        <p class="rateTime">{{v.rateTime | date}}</p>
                        <p class="userScore">
                            <s-star :score = 'v.score'></s-star>&nbsp;&nbsp;
                            <span v-if="v.deliveryTime">{{v.deliveryTime}}分钟送达</span>
                        </p>
                        <p class="itemtext">{{v.text}}</p>
                        <div class="satisfy">
                            <span v-if="v.rateType == 0" class="good"><i class="iconfont icon-damuzhion"></i></span>
                            <span v-else="v.rateType == 1" class="bad"><i class="iconfont icon-down"></i></span>
                            <div v-for="(v,k) in v.recommend" class="foods" v-if="k<3">
                                <span>{{v}}</span>
                            </div>
                        </div>
                    </div>
                </li>
            </ul> 
        </div>
    </div>
</template>
<script>
	import {mapGetters} from 'vuex'
  import mStar from '../star/Star_m.vue'
  import sStar from '../star/Star_s.vue'
	export default {
    data(){
      return {
          all: true,
          yes: -1,
          flag: false,
          cc:'getAll'
      }
    },
  	created(){
        this.$store.dispatch('getRatings');
        this.$nextTick(()=>{  
            this._initRatings();
        })
  	},
      //年月日
    filters:{
      date:function(value){
        function toDouble(n){
          return n<10?'0'+n:n;
        }
        var d=new Date(value);
        return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+" "+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
      }
    },
    components:{
      'm-star':mStar,
      's-star':sStar
    },
	computed:{
		...mapGetters([
			'seller',
			'ratings'
		]),
        ratLists(){
            if(this.flag){
                return this.ratings.filter((item) => {
                     return item.text
                })
            }
            return this.ratings;
        },
        countnum(){
                // console.log(1)
            return this.ratings.filter((item) => {
                return item.rateType === 0
                }).length;
        }
	},
    watch:{
      ratings(){
        this.$nextTick(()=>{
          this._initRatings();
        })
      }
    },
    methods:{
      changeFlag(){
        this.flag=!this.flag
        this.refresh()
      },
      refresh(){
        setTimeout(()=>{
          this.ratingsWrapper.refresh()
        },20)
      },
      _initRatings(){
        this.ratingsWrapper = new BScroll ( this.$refs.ratingsWrapper);
      },
      ok(){
        this.yes=0
        this.all=false
        this.refresh()
        this.cc='ok'
      },
      no() {
        this.yes = 1
        this.all = false
        this.refresh()
        this.cc='no'
      },
      getAll() {
        this.yes = -1
        this.all = true
        this.refresh()
        this.cc='getAll'
      },
    }
	}
</script>

<style lang="less" scoped>
	.ratings{
        .rating-content{
         	width:100%;
         	height: 2.3rem;
         	display: flex;
         	.ratingleft{
         		width:2.75rem;
                margin-left:0.36rem;
                text-align:center;
                .numscore{
                	font-size: 0.48rem;
                	color:rgb(253,153,0);
                    line-height: 0.56rem;
                    padding-top: 0.36rem;
                };
                .numscoretext{
                	font-size: 0.24rem;
                	color:rgb(7,17,27);
                	padding-top: 0.16rem;
                };
                .rankRate{
                	font-size: 0.20rem;
                	color:rgba(7,17,27,0.5);
                    line-height: 0.36rem;
                    padding-top: 0.2rem;
                }
            }
            .ratingright{
                width:4.74rem;
                margin-top: 0.48rem;
                margin-bottom: 0.36rem;
                padding-left: 0.48rem;
                border-left:0.01rem solid rgba(0,0,0,0.2);
                .serviceScore,.foodScore,.deliveryTime{
                    margin-bottom: 0.16rem;
                    display: flex;
                    .text{
                        font-size: 0.24rem;
                        color: rgb(7, 17, 27);
                        line-height: 0.36rem;
                        margin-right: 0.12rem;
                    }
                    .num{
                        font-size: 0.24rem;
                        color: rgb(255,153,0);
                        line-height: 0.36rem;
                        padding-left:0.18rem;
                    }
                }
                .deliveryTime{
                    margin-bottom: 0;
                    .time{
                        font-size: 0.24rem;
                        color: rgb(147, 153, 159);
                        line-height: 0.36rem;
                    }
                }
            }
        }
        .line{
            width:100%;
            background-color:rgba(7, 17, 27, 0.1);
            height:0.3rem;
            border-top:1px solid rgba(27, 17, 27, 0.1);
            border-bottom:1px solid rgba(27, 17, 27, 0.1);
        }
        .btn-content{
                padding:0.36rem 0.36rem 0.32rem 0.36rem;
                border-bottom:0.02rem solid rgba(0,0,0,0.1);
                .btn{
                    width:100%;
                    height:1rem;
                    display: flex;
                    border-bottom:0.02rem solid rgba(0,0,0,0.1);
                    .btn1{
                        width:1.2rem;
                        height:0.5rem;
                        margin-top:0.1rem;
                        font-size:0.24rem;
                        background-color:#3399ff;
                        color: white;
                        text-align:center;
                        line-height: 0.5rem;
                        &.getAll{
                            background:rgb(0, 160, 220);
                        }
                    }
                    .btn2{
                        width:1.2rem;
                        height:0.5rem;
                        font-size:0.24rem;
                        background-color:#99ccff;
                        margin-top:0.1rem;
                        color:white;
                        text-align:center;
                        line-height: 0.5rem;
                        margin-left:0.18rem;
                        &.ok{
                            background-color: rgba(0, 160, 220, 0.5);
                        }
                    }

                    .btn3{
                        width:1.2rem;
                        height:0.5rem;
                        font-size:0.24rem;
                        background-color:#ccc;
                        margin-top:0.1rem;
                        color:white;
                        text-align:center;
                        line-height: 0.5rem;
                        margin-left:0.2rem;
                        &.no{
                            background-color: rgba(77, 85, 93, 0.5);
                        }
                    }
                }
                .btn-text{
                    height: 0.8rem;
                    line-height:1.1rem;
                    color: #ccc;
                    font-weight:600;
                .icon{
                    font-size: 0.45rem;
                    display:inline-block;
                    color: rgba(147, 153, 159,0.6);
                    &.checked{
                        color: rgb(0, 160, 220);
                    }
                }
                .text{
                    font-size: 0.3rem;
                    font-weight:600;    
                }
            }
        }
        .ratingsinfo{
          height: 4.5rem;
          overflow: hidden;
          .menu-li{
            height: 2.2rem;
            margin:0 0.36rem;
            border-bottom:0.02rem solid rgba(0, 0, 0, 0.2);
            display: flex;
            .headDiv{
              height: 2.0rem;
              width: 0.56rem;
              .headImg{
                display: block;
                width: 0.56rem;
                height: 0.56rem;
                border-radius: 50%;
                margin-top:0.36rem;
              }
            }
            .rightdiv{
              padding-top:0.4rem;
              margin-left: 0.24rem;
              .itemusername{
                font-size: 0.2rem;
              };
              .rateTime{
                position: absolute;
                right: 0.5rem;
              }
              .userScore{
                padding-top:0.06rem;
                padding-bottom: 0.1rem;
                font-size: 0.2rem;
                display: flex;
                color: #ccc;
              }
              .itemtext{
                font-size: 0.24rem;
              }
              .satisfy{
                display: flex;
                .good{
                  color: rgb(0, 160, 220);
                  font-size: 0.24rem;
                  line-height: 0.84rem;
                }
                .bad{
                  color: rgb(147, 153, 159);
                  font-size: 0.24rem;
                  line-height: 0.84rem;
                }
                .foods{
                  display: flex;
                  span{
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow:ellipsis;
                    margin:0.16rem;
                    border:0.02rem solid rgba(7, 17, 27, 0.1);
                    border-radius: 0.02rem;
                    background-color: white;
                    font-size: 0.18rem;
                    color: rgb(147, 153, 159);
                    padding: 0.12rem;
                    width: 1rem;
                    margin-bottom: 0.36rem;
                  }
                }
              }
            }
          }
        }
	}
</style>


