import types from '../types.js'
import axios from 'axios'

// 相当于.vue里面的data
const state = {
	seller:{},
	detailshow:false
}
// 返回数据
const getters = {
	seller(state){
		return state.seller;
	},
	detailshow(state){
		return state.detailshow;
	},
	closedetail(state){
		return state.closedetail;
	}

}
// 接收componens里面传过来的方法并提交给mutations执行
const actions = {

	getSeller({commit,state}){
		axios.get('../../../static/data.json').then( resp => {
			if( resp.data.errno === 0 ){
				commit(types.GET_SELLER,resp.data.seller)
			}
		})
	},
	showdetail({commit}){
        commit(types.GET_DETAIL);
    },
    closedetail({commit}){
    	commit(types.CLOSE_DETAIL);
    }
}
// 执行具体动作
const mutations = {
	[types.GET_SELLER](state,data){
		state.seller = data;
	},
	[types.GET_DETAIL](state){
        state.detailshow=true;
    },
    [types.CLOSE_DETAIL](state){
    	state.detailshow=false;
    }
}
export default {
	state,
	getters,
	actions,
	mutations
}



