<template>			
	<transition enter-active-class="animated slideInRight" leave-active-class="animated slideOutRight">
		<div class="goodsdetail" v-if="foodshow" ref='imgWrapper'>
			<div class="detail-content">
				<div class="image-header">
					<img :src="food.image" alt="">
				</div>
				<div class="hide-food" @click = 'hidefood'>
					 <i class="icon iconfont icon-shiliangzhinengduixiang4"></i>
				</div>
				<div class="basicinfo">
					<p class="foodinfoname">{{food.name}}</p>
					<p class="foodinfocount">月售{{food.sellCount}}份&nbsp;&nbsp;好评率{{food.rating}}%</p>
					<div class="foodinfoprice">
						<p><span class="yen">&yen;</span>{{food.price}}</p>
						<p><span class="oldPrice" v-if="food.oldPrice>0">&yen;</span>{{food.oldPrice}}</p>
						<div class="addcart" @click="showadd($event)"v-show='!food.count||food.count===0'>加入购物车</div>
						<div class="symbol">
							<cartcontrol :food='food'></cartcontrol>
						</div>
						
					</div>
				</div>
				<div class="middleline"></div>
				<div class="product-info">
					<p class="product-name">商品介绍</p>
					<div class="product-text">{{food.info}}</div>
				</div>
				<div class="middleline"></div>
			    <div class="btn-content">
			    	<p class="product-evaluate">商品评价</p>
			        <div class="btn">
				        <div class="btn1" @click="getAll" :class="{'getAll':'getAll'===cc}">全部 <span>{{food.ratings.length}}</span></div>
				        <div class="btn2" @click="ok":class="{'ok':'ok'===cc}">满意<span>{{countnum}}</span></div>
				        <div class="btn3" @click="no":class="{'no':'no'===cc}">不满意<span>{{food.ratings.length-countnum}}</span></div>
			        </div>
			        <div class="btn-text">
			          	<i class="iconfont icon-duihao" :class="{'checked':flag}" @click="changeFlag"></i>
		                <span>只看有内容的评价</span>
			        </div>
			    </div>
			    <div class="midline"></div>
			    <div class="ratingsinfo">
			        <ul>
				        <li v-for="(v,k) in ratLists":key="k" class="menu-list" v-show="all || yes === v.rateType">
				        	<div class="bottom">
								<div class="userinfo">
									<div class="rateTime">{{v.rateTime | date}}</div>
									<div>
										<p class="username">{{v.username}}</p>
										<img :src="v.avatar">
									</div>
								</div>
								<div class="satisfy">
                                    <span v-if="v.rateType == 0" class="good"><i class="iconfont icon-damuzhion"></i></span>
                                    <span v-else="v.rateType == 1" class="bad"><i class="iconfont icon-down"></i></span>
                                    <span class="text">{{v.text}}</span>
								</div>
							</div>
				        </li>
			        </ul> 
			    </div>
			</div>
			<shopcart></shopcart>
		</div>
	</transition>	
</template>
<script>
	import {mapGetters} from 'vuex'
	import shopcart from '../Shopcart/Shopcart.vue'
	import cartcontrol from '../Cartcontrol/Cartcontrol.vue'
	import Vue from'vue'
	export default {
		data(){
		    return {
		        all: true,
		        yes: -1,
		        flag: false,
		        cc:'getAll'
		    }
	    },
		created(){
			this.$store.dispatch('getRatings');
		},

		filters:{
		    date:function(value){
		        function toDouble(n){
		          return n<10?'0'+n:n;
		        }
		        var d=new Date(value);
		        return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+" "+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
		    }
	    },
		computed:{
			...mapGetters([
				'foodshow',
				'food',
			]),
			ratLists(){
				if(this.flag){
					return this.food.ratings.filter((item)=>{
						return item.text
					})
				}
				return this.food.ratings;
			},
			countnum(){
                // console.log(1)
                return  this.food.ratings.filter((item) => {
                        return item.rateType === 0
                    }).length;
            }
		},
		watch:{
			food(){
				setTimeout(() => {
		            if( !this.foodDetailWrap ){
		            	this.foodDetailWrap = new BScroll (this.$refs.imgWrapper, {
		            		click : true
		            	})
		            }else{
		            	this.foodDetailWrap.refresh();
		            }
				},200)
			}
		},
		methods:{
			showadd(e){
				if(!e._constructed){
					return;
				}
				Vue.set(this.food,'count',1);
			},
			hidefood(){
				this.$store.dispatch('hidefood');
			},
			refresh(){
		        setTimeout(()=>{
		          	this.foodDetailWrap.refresh()
		        },20)
		    },
	      	changeFlag() {
                this.flag = !this.flag;
                this.refresh()
            },
		    ok(){
		        this.yes=0
		        this.all=false
		        this.refresh()
		        this.cc='ok'
	     	},
	      	no(){
		        this.yes = 1
		        this.all = false
		        this.refresh()
		        this.cc='no'
	      	},
	      	getAll(){
		        this.yes = -1
		        this.all = true
		        this.refresh()
		        this.cc='getAll'
	      	},
		},
		components:{
	     	shopcart,
	    	cartcontrol
     	},
	}
</script>

<style lang="less" scoped>
	.goodsdetail{
		background-color: white;
		position:fixed;
		top: 0;
		z-index: 999;
		left: 0;
		right: 0;
		bottom: 0;
		.detail-content{
			.image-header{
				position: relative;
				width: 100%;
				height: 0;
				padding-bottom:100%;
				
				img{
					position:absolute;
					width: 100%;
					height: 100%;
					left: 0;
					top:0;
				}
			}
			.hide-food{
				position: fixed;
				top:10px;
				left: 10px;
				width: 10px;
				height: 10px;
				padding:10px;
				color:white;
			}
			.basicinfo{
				padding-top: 0.36rem;
				padding-left: 0.36rem;
				position: relative;
				.foodinfoname{
					font-size: 0.28rem;
					font-weight: 700;
					color: rgb(7, 17, 27);
					line-height: 0.28rem;
				}
				.foodinfocount{
					font-size: 0.2rem;
					color: rgb(147, 153, 159);
					line-height: 0.2rem;
					padding-top: 0.16rem;
					padding-right: 0.24rem;
				}
				.foodinfoprice{
					font-weight: 700;
					font-size: 0.28rem;
					color: rgb(240, 20, 20);
					line-height: 0.48rem;
					.symbol{
						position:absolute;
						right: 1rem;
						bottom:0.3rem;
					} 
					.addcart{
						position:absolute;
						right:0.36rem;
						bottom: 0.1rem;
						width: 1.48rem;
						height: 0.48rem;
						border-radius: 0.24rem;
						background: rgb(0, 160, 220);
						color: white;
						text-align: center;
						line-height: 0.48rem;
						font-size: 0.2rem;
						z-index: 999;
					}
				}
			}
			.middleline{
	        	width: 100%;
	        	height: 0.32rem;
	        	background-color: rgba(7, 17, 27, 0.1);
	        	border-top:1px solid rgba(7, 17, 27, 0.1);
	        	border-bottom: 1px solid rgba(7, 17, 27, 0.1);
	        	margin-top: 0.36rem;
	        	}
        	.product-info{
        		padding:0.36rem 0.36rem  0  0.36rem; 
        		.product-name{
        			font-size: 0.28rem;
        			font-weight: 700;
        			color: rgb(7, 17, 27);
        			line-height: 0.28rem;
        		}
        		.product-text{
        			font-size: 0.24rem;
        			padding-top: 0.12rem;
        			font-weight: 200;
        			color: rgb(77, 85, 93);
        			line-height: 0.48rem;
        		}
        	}
        	.btn-content{
		        padding:0.36rem;
		        border-bottom:0.02rem solid rgba(0,0,0,0.1);
	          	.product-evaluate{
	        		font-size: 0.28rem;
	        		font-weight: 700;
	        		color: rgb(7, 17, 27);
	        		line-height: 0.28rem;
	        		padding-bottom: 0.36rem;
	          	}
		        .btn{
			        width:100%;
			        height:1rem;
			        display: flex;
			        border-bottom:0.02rem solid rgba(0,0,0,0.1);
		          .btn1{
			            width:1.2rem;
			            height:0.5rem;
			            margin-top:0.1rem;
			            font-size:0.24rem;
			            background-color:#3399ff;
			            color: white;
			            text-align:center;
			            line-height: 0.5rem;
			            &.getAll{
                            background:rgb(0, 160, 220);
                        }
		         	}
		          .btn2{
			            width:1.2rem;
			            height:0.5rem;
			            font-size:0.24rem;
			            background-color:#99ccff;
			            margin-top:0.1rem;
			            color:white;
			            text-align:center;
			            line-height: 0.5rem;
			            margin-left:0.18rem;
			            &.ok{
                            background-color: rgba(0, 160, 220, 0.5);
                        }
			        }

		          	.btn3{
			            width:1.2rem;
			            height:0.5rem;
			            font-size:0.24rem;
			            background-color:#ccc;
			            margin-top:0.1rem;
			            color:white;
			            text-align:center;
			            line-height: 0.5rem;
			            margin-left:0.2rem;
			            &.no{
                            background-color: rgba(77, 85, 93, 0.5);
                        }
			        }
	        	}
		        .btn-text{
		            height: 0.8rem;
		            line-height:1.2rem;
		            font-size:0.24rem;
		            color: rgb(147, 153, 159);
		            font-weight:600;
		            .el-icon-circle-check{
			            font-size: 0.35rem;
			            display:inline-block;
			            vertical-align:middle;
			            color: rgb(147, 153, 159);
		            }
		            .checked{
		              	color:rgb(0, 160, 220);
		            }
		        }
        	}
        	.ratingsinfo{
        		.menu-list{
        			.bottom{
        				margin: 0.32rem;
                        border-bottom:1px solid rgba(7, 17, 27, 0.1);
        				.userinfo{
        					display: flex;
        					justify-content:space-between;
        					padding-bottom: 0.12rem;
        					position: relative;
        					.username{
        						position: absolute;
        						right: 0.8rem;
        						top:0.1rem;

        					}
        					img{
								border-radius: 50%;
                           	 	width: 0.4rem;
                            	height: 0.4rem;
                            	margin-left: 0.12rem;
                        	}
        				}
        				.satisfy{
                            display: flex;
                            font-size: 0.24rem;
                            line-height: 0.48rem;
                            .good{
                                color: rgb(0, 160, 220); 
                                padding-right: 0.08rem;                                 
                            }
                            .bad {
                                color: rgb(147, 153, 159);
                                padding-right: 0.08rem;                                 
                            }
                            .text {
	                            line-height: 0.32rem;
	                            color:rgb(7, 17, 27);
                         	}
                        }
        			}
        			
        		}
        	}

		}
	}
</style>


