<template>
	<div class="goods">
	<!--左边-->
		<div class="goods-menu" ref="menuWrapper">
			<ul>
				<li @click='selectMenu(index,$event)' v-for="(item,index) in goods" class="menu-list" :class = "{'current' : index === currentIndex}">
					<span class="text">
						{{item.name}}
					</span>
				</li>
			</ul>
		</div>
		<div class="goods-foods" ref="foodsWrapper">
			<ul>
				<li v-for="(item,index) in goods" class="food-list food-list-hook">
					<p class="itemName">{{item.name}}</p>
					<ul>
						<li v-for="(food,index) in item.foods" class="food"@click='showfood(food,$event)'>
							<img :src="food.icon" class="foodicon">
							<div class="foodinfo">
								<p class="foodinfoname">{{food.name}}</p>
								<p class="foodinfocount">月售{{food.sellCount}}份&nbsp;好评率{{food.rating}}%</p>
								<div class="foodinfoprice">
									<p><span class="yen">&yen;</span>{{food.price}}</p>
									<cartcontrol :food='food' class="symbol"></cartcontrol>
								</div>
							</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<foodDetail></foodDetail>
		<shopcart></shopcart>
	</div>
</template>

<script>
import {mapGetters} from 'vuex'
import axios from 'axios'
import foodDetail from '../detail/foodDetail.vue'
import shopcart from '../Shopcart/Shopcart.vue'
import cartcontrol from '../Cartcontrol/Cartcontrol.vue'
	export default {
		data(){
			return {
				listHeight : [],
				scrollY : 0
			}
		},
		computed:{
			...mapGetters([
					'goods',
					
				]),
				currentIndex(){
					for(let i=0; i<this.listHeight.length;i++){
						let height1=this.listHeight[i];
						let height2=this.listHeight[i+1];
						if(this.scrollY >=height1 && this.scrollY < height2){
							return i;
						}
					}
					return 0;
			}
		},
		created(){
			this.$store.dispatch('getGoods');
			this.$nextTick( () => {
				setTimeout(() => {
					this._initMenu();
					this._initFood();
					this._calcHeight();
				},1000)
			})
		},
		methods:{
			showfood(f,e){
				if( !e._constructed ){
					return;
				};
				this.$store.dispatch('showfood',f);
			},
			selectMenu(key,e){
				if(!e._constructed){
					return;
				}
				let foodList=this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
				let el=foodList[key];
				this.foodBS.scrollToElement(el,300);
			},
			_calcHeight(){
				let foodList=this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
				let height=0;
				this.listHeight.push(height);
				for(let i=0;i<foodList.length;i++){
					height += foodList[i].clientHeight;
					this.listHeight.push(height);
				}
			},
			_initMenu(){
				this.menuBS = new BScroll(this.$refs.menuWrapper,{click:true});
			},
			_initFood(){
				this.foodBS=new  BScroll(this.$refs.foodsWrapper,{
					probeType:3,
					click:true
				});
				this.foodBS.on('scroll',(pos)=>{
					this.scrollY=Math.abs(Math.round(pos.y));
				})
			},
		},
		components:{
	     	foodDetail,
	     	shopcart,
	     	cartcontrol
        }
	}	
</script>

<style lang='less' scoped>
	.goods{
		position: absolute;
		top: 3.48rem;
		bottom: 0.92rem;
		width: 100%;
		display: flex;
		.goods-menu{
			flex:0 0 1.6rem;
			width: 1.6rem;
			background: #f3f5f7;
			overflow: hidden;
			.menu-list{
				display:table;
				height:1.08rem;
				background: #f3f5f7;
				padding: 0 0.24rem;

				&.current{
					background: rgb(255, 255, 255);
				}
				.text{
					text-align: center;
					vertical-align: middle;
					display: table-cell;
					width: 1.12rem;
					border-bottom:1px solid rgba(7, 17, 27, 0.1);
					font-weight: 200;
					line-height: 0.28rem;
					font-size: 0.24rem;
					color: rgb(7, 17, 27);
				}
				
			}
		}
		.goods-foods{
			flex:1;
			overflow: hidden;
			width: 5.9rem;
			.itemName{
				font-size: 0.24rem;
				color:rgb(147, 153, 159);
				padding-left:0.28rem;
				height: 0.52rem;
				line-height: 0.52rem;
				background-color: #f3f5f7;
				border-left: 0.02rem solid #d9dde1;
			}
			.food{
				display: flex;
				border-bottom: 0.02rem solid rgba(7, 17, 27, 0.1);
				margin: 0.15rem 0.36rem;
				padding-bottom: 0.36rem;
				.foodicon{
					width: 1.1rem;
					height: 1.1rem;
				}
				.foodinfo{
					margin-left: 0.2rem;
					.foodinfoname{
						font-size: 0.28rem;
						line-height: 0.28rem;
						padding-top: 0.04rem;
					}
					.foodinfocount{
						font-size: 0.2rem;
						line-height: 0.2rem;
						padding-top: 0.16rem;
						color:rgb(147, 153,159);
					}
					.foodinfoprice{
						width: 3.5rem;
						font-size: 0.28rem;
						line-height: 0.28rem;
						padding-top: 0.16rem;
						color: red;
						.yen{
							font-size: 0.2rem;
						}
						.symbol{
							position: relative;
							left:2.4rem;
							bottom:0.35rem; 
						}
					}
				}
			}
		}


	}
</style>


