<template>
  <div class="cartcontrol">
    <transition name="slide-fade">
      <div class="reduce" v-show = 'food.count' @click.stop = 'decrement(food,$event)'><i class="icon iconfont icon-jianhao"></i></div>
    </transition>  
      <div class="count" v-show = 'food.count'>{{food.count}}</div>
      <div class="add" @click.stop = 'increment(food,$event)'> <i class="icon iconfont icon-jiahao"></i></div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import {mapGetters} from 'vuex'
  export default {
    props:['food'],
    methods:{
      increment(f,e){
        if(!e._constructed){
          return
        }
        if( !this.food.count ){
          Vue.set(this.food , 'count' ,1);
        }else{
          this.food.count++;
        }
      },
      decrement(f,e){
        if(!e._constructed){
          return
        }
        if( this.food.count ){
          this.food.count--;
        }
      }
    }
  }
</script>

<style lang='less' scoped>
  .cartcontrol{
    width:0.9rem;
    height:0.3rem;
    position: relative;
    // right: -2rem;
    // top:-0.35rem;
    .slide-fade-enter-active {
      transition: all .3s ease;
    }
    .slide-fade-leave-active {
      transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .slide-fade-enter, .slide-fade-leave-to{
      transform: translateX(10px);
      opacity: 0;
    }
    .add{
      position: absolute;
      right:0;
      color:rgb(0, 160, 220);
      i{
        font-size:0.48rem;
        line-height:0.48rem;
        margin-left:0.24rem;
      }
    }
    .reduce{
      position: absolute;
      left: 0;
      color: rgb(0, 160, 220);

      i{
        font-size: 0.48rem;
        line-height: 0.48rem;
      }
    }
    .count{
      position:absolute;
      left: 0.5rem;
      top:0.05rem;
      color: rgb(147, 153, 159);
      line-height: 0.48rem;
    }
    div{
      width:0.3rem;
      height: 0.3rem;
      line-height:0.3rem;
      text-align: center;
      display: inline-block;
    }
  }
</style>


