<template>
	<transition enter-active-class="animated slideInRight" leave-active-class="animated slideOutRight">
		<div class="detail" v-show="detailshow">
			<div class="content">
				<h1 class="title">{{seller.name}}</h1>
				<div class="stars">
					<star :score = 'seller.score'></star>
				</div> 
				<div class="discounts">
					<div class="line"></div>
					<div class="info">优惠信息</div>
					<div class="line"></div>
				</div>
				<div class="discounts-detail">
					<ul v-if="seller.supports" class="supports">
						<li class="support-item" v-for="item in seller.supports">
			                <span class="icon" :class="iconClassMap[item.type]"></span>
			                <span class="text">{{item.description}}</span>
			             </li>
					</ul>
				</div>
				<div class="discounts">
					<div class="line"></div>
					<div class="info">商家公告</div>
					<div class="line"></div>
				</div>
				<div class="bulletin">{{seller.bulletin}}</div>
				<div class="detail-close">
					<i class="el-icon-close" @click='close'></i>
				</div>
			</div>
		</div>
	</transition>	
</template>
<script>
	import {mapGetters} from 'vuex'
	import Star from '../star/Star.vue'
	export default{
		components:{
			'star':Star
		},
		computed:{
			...mapGetters([
					'detailshow',
					'seller'
				])
		},
		created(){
			this.$store.dispatch('getSeller');
			this.iconClassMap=['decrease', 'discount', 'special', 'invoice', 'guarantee']
		},
		methods:{
			close(){
				this.$store.dispatch('closedetail');
			}
		}
	}
</script>
<style lang="less" scoped>
	.detail{
		position:fixed;
		top:0;
		left:0;
		right:0;
		bottom:0;
		background:rgba(7, 17, 27,0.8);
		z-index: 999;
		padding: 1.28rem 0.72rem 0.64rem 0.72rem;
		color: rgb(255, 255, 255);
		// filter:blur(0.1rem);
		.content{
			.title{
				font-size: 0.32rem;
				font-weight: 700;
				color: rgb(255, 255, 255);
				line-height: 0.32rem;
				text-align: center;
			}
			.stars{
				text-align: center;
				padding-top:0.5rem;

			}
			.discounts{
				display:flex;
				position: relative;
				top:0.8rem;
				.line{
					flex:1;
					position:relative;
					top:0.3rem;
					border-top: 2px solid rgba(255, 255, 255, 0.2);
					width: 2.24rem;
					height: 0;
				}
				.info{
					margin: 0.2rem;
					font-size: 0.28rem;
					color:rgb(255, 255, 255);
					font-weight:700;
				}
			}
				.supports{
					width:5rem;
					height:2rem;
					margin-top:1rem;
					.icon{
						display:inline-block;
						width:0.32rem;
            			height:0.32rem;
						margin-right:0.04rem;
						background-size:0.32rem  0.32rem;
						background-repeat:no-repeat;
						vertical-align:top;
						padding: 0.12rem;
						&.decrease{
							background-image: url("../../imgs/decrease.png");
						}
						&.discount{
							background-image: url("../../imgs/discount.png");
						}
						&.guarantee{
							background-image: url("../../imgs/guarantee.png");
						}
						&.invoice{
							background-image: url("../../imgs/invoice.png");
						}
						&.special{
							background-image: url("../../imgs/special.png");
						}
					}
					.text{
							line-height:0.32rem;
							font-size: 0.24rem;
							font-weight: 200;
							color: rgb(255, 255, 255);
					}
				}
			.detail-close{
				position: relative;
				width: 0.32rem;
				height: 0.32rem;
				font-size: 0.64rem;
				color: rgba(255, 255, 255, 0.5);
				margin :0.64rem  auto 0 auto;
			}
		}
		.bulletin{
			font-size: 0.24rem;
			font-weight: 200;
			color: rgb(255, 255, 255);
			line-height: 0.48rem;
			margin-top: 1rem;
			padding-left: 0.24rem;
			width: 5.55rem;
				
		}
	}
</style>


