<template>
	<div class="star">
		<span v-for="active in itemlist" class="list" :class='active' ></span>
	</div>
</template>
<script>
	const LENGTH=5;
	const STAR_ON='on';
	const STAR_HALF='half';
	const STAR_OFF='off';
	export default{
		props:['score'],
		computed:{
		// itemlist用来遍历span
			itemlist(){
	 // 定义一个空数组用来存变量
				let result=[];
				let integer=Math.floor(this.score);
				let decimal=this.score-integer;
				for(let i=0;i<integer;i++){
					result.push(STAR_ON);
				};
				if(decimal>=0.5){
					result.push(STAR_HALF);
				};
				while (result.length<LENGTH){
					result.push(STAR_OFF);
				};
				return result;
			}
		}
	}
</script>

<style lang='less' scoped>
	.star{
		font-size: 0.36rem;
		.list{
			display: inline-block;
			width: 0.36rem;
			height: 0.36rem;
			margin-right: 0.1rem;
			&.on{
				background-image: url('star_on.png');
				background-size: 0.36rem 0.36rem;
				background-repeat: no-repeat;
			}
			&.half{
				background-image: url('star_half.png');
				background-size: 0.36rem 0.36rem;
				background-repeat: no-repeat;
			}
			&.off{
				background-image: url('star_off.png');
				background-size: 0.36rem  0.36rem;
				background-repeat: no-repeat;
			}
		}
	}
</style>


