//数据仓库的定义
import types from '../types.js'
import axios from 'axios'


//定义数据状态
const state = {
	goods:[],
	foodshow : false,
	food : {}
}


// 暴露get方法 提供组件数据获取接口

const getters = {
	goods(state){
		return state.goods;
	},
	food(state){
		return state.food;
	},
	foodshow(state){
		return state.foodshow;
	},
}

// 定义操作方法，执行数据突变提交
const actions = {
	getGoods({commit,state}){
		axios.get('../../../static/data.json').then(resp => {
			if(resp.data.errno===0){
				commit(types.GET_GOODS,resp.data.goods)
			}
		})
	},
	showfood({commit},food){
		commit(types.SHOW_FOOD,food);
	},
	hidefood({commit}){
		commit(types.HIDE_FOOD);
	}
}

// 注册数据突变后的修改方法
const mutations = {
	[types.HIDE_FOOD](state){
		state.foodshow = false;
	},
	[types.SHOW_FOOD](state,food){
		state.foodshow = true;
		state.food = food;
	},
	[types.GET_GOODS](state,data){
		state.goods=data;
	}
}
export default {
	state,
	getters,
	actions,
	mutations
}



